import React from 'react';
import { Link } from 'react-router-dom'
import AppState from '../AppState'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Grid from '@material-ui/core/Grid'
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';

const useStyles = makeStyles((theme) => ({
  tabs: {
    backgroundColor: 'white'
  },
  tab: {
    backgroundColor: 'white',
    color: '#3c4b5a',
    fontSize: '80%',
    fontWeight: 'bold',
  },
  root: {
    position: 'relative',
    backgroundColor: 'white',
    margin: '50px 0',
    borderRadius: '15px',
    overflow: 'hidden'
  },
  btn: {
    position: 'relative',
    display: 'flex',
    alignItems: 'center',
    textDecoration: 'none',
    margin: '5px',
    width: 'calc(100% - 10px)',
    borderRadius: '15px',
    border: '1px solid #f0f4f5',
    background: '#f0f4f5',
    color: '#3c4b5a',
    textAlign: 'center',
    padding: '17px 22px',
    cursor: 'pointer',
    '&:hover': {
      color: '#0056b3',
      background: '#fff',
    }, 
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          {children}
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function LinkTab(props) {
  const classes = useStyles();
  return (
    <Tab
      component="a"
      className={classes.tab}
      onClick={(event) => {
        event.preventDefault();
      }}
      {...props}
    />
  );
}


export default function Chooser(props) {
  const context = React.useContext(AppState)
  const [value, setValue] = React.useState(0);
  const classes = useStyles();

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }

  const uniqRoads = context.uniqRoads
  const uniqAreas = context.uniqAreas

  const uniqDist = ['30','40','50','60','70','80','90']
  const uniqPrices = [
          'до 250 000 р.',
          'до 500 000 р.',
          'до 750 000 р.',
          'до 1 000 000 р.',
          'до 1 500 000 р.',
          'до 3 000 000 р.',
          'до 5 000 000 р.',
          'до 10 000 000 р.',
          'до 20 000 000 р.',
        ] 

  const arrRoads = []
  const arrAreas = []
  const arrDist = []
  const arrPrices = []

  for (let i=0;i<uniqRoads.length;i++) {
    arrRoads.push(
      <Grid key={'ARRROADS'+i} item xs={12} sm={6} md={4} lg={3}>
        <Link
          to='/villages'
          onClick={()=>context.selectOnlyRoad(uniqRoads[i])}
          className={classes.btn}
        >
          <h5>{uniqRoads[i]}</h5>
        </Link>
      </Grid>
    )
  }

  for (let i=0;i<uniqAreas.length;i++) {
    arrAreas.push(
      <Grid key={'ARRAREAS'+i} item xs={12} sm={6} md={4} lg={3}>
        <Link
          to='/villages'
          onClick={()=>context.selectOnlyArea(uniqAreas[i])}
          className={classes.btn}
        >
          <h5>{uniqAreas[i]}</h5>
        </Link>
      </Grid>
    )
  }

  for (let i=0;i<uniqDist.length;i++) {
    arrDist.push(
      <Grid key={'ARRDIST'+i} item xs={12} sm={6} md={4} lg={3}>
        <Link
          to='/villages'
          onClick={()=>context.selectOnlyDist(uniqDist[i])}
          className={classes.btn}
        >
          <h5>до {uniqDist[i]} км.</h5>
        </Link>
      </Grid>
    )
  }

  for (let i=0;i<uniqPrices.length;i++) {
    arrPrices.push(
      <Grid key={'ARRPRICE'+i} item xs={12} sm={6} md={4} lg={3}>
        <Link
          to='/villages'
          onClick={()=>context.selectOnlyPrice(uniqPrices[i])}
          className={classes.btn}
        >
          <h5>Участок {uniqPrices[i]}</h5>
        </Link>
      </Grid>
    )
  }

  return (
    <div className={classes.root}>

      <AppBar color='inherit' position="static" style={{boxShadow: 'none',}}>
        <Tabs
          variant="fullWidth"
          value={value}
          onChange={handleChange}
          aria-label="nav tabs example"
          className={classes.tabs}
        >
          <LinkTab label="Шоссе" />
          <LinkTab label="Районы" />
          <LinkTab label="от МКАД" />
          <LinkTab label="Цена" />
        </Tabs>
      </AppBar>

      <TabPanel value={value} index={0}>
        <Grid container>
          {arrRoads}
        </Grid>
      </TabPanel>

      <TabPanel value={value} index={1}>
        <Grid container>
          {arrAreas}
        </Grid>
      </TabPanel>

      <TabPanel value={value} index={2}>
        <Grid container>
          {arrDist}
        </Grid>
      </TabPanel>

      <TabPanel value={value} index={3}>
        <Grid container>
          {arrPrices}
        </Grid>
      </TabPanel>

    </div>
  );
}
